.TodoSearch {
  background: #f9fbfc;
  border-radius: 4px;
  border: 2px solid #202329;
  height: 50px;
  font-size: 24px;
  text-align: center;
  font-family: "Montserrat";
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  min-width: 500px;
}

.TodoSearch::placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61dafa;
}

.TodoSearch:disabled {
  opacity: 25%;
}

.TodoSearch--loading {
  transition: 1s;
}
